import React, { useState, useEffect } from 'react'
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import NumberFormat from 'react-number-format'
import { useForm } from '../../hooks/useForm';
import { alert_warning } from '../../../helpers/Notificacion';
import callApi from '../../../helpers/conectorApi';
export const FilaItemContraparte = ({ listContraparte, movimiento, llaveId = 0, setDetalleContraparte, detalleContraparte, cuenta_contableId, setAsientos, asientos, setAddNuevo, editarItem = false, monto = 0, setEditarItem }) => {
    const [NombreContraparte, setNombreContraparte] = useState('');
    const [bloquearHaber, setBloquearHaber] = useState(false);
    const [contraParteEdit, setContraParteEdit] = useState(null);

    const [values, handleOnChange, , setValues] = useForm({
        contraparte: {},
        monto
    });

    const onchangeCuenta = (event, nuevoValor) => {
        setValues({ ...values, contraparte: nuevoValor });
        setContraParteEdit(nuevoValor);
    }
    const handleKeyPress = (event) => {
        if (event.key === 'Enter') {
            if (llaveId > 0) {
                handleEditItemContraparte();
            } else {
                addItemContraparte();
            }
        }
    }
    const addItemContraparte = () => {
        if (values) {
            let { id = 0, nombre } = values.contraparte;
            if (Number(id) > 0) {
                let { monto = "0" } = values;

                monto = String(monto).replace("Q ", "");
                monto = String(monto).replace(",", "");
                if (Number(monto) > 0) {
                    let itemDetalle = detalleContraparte || [];
                    itemDetalle.push({
                        llaveId: id,
                        monto,
                        nombre,
                        editar: false
                    });
                    setDetalleContraparte(itemDetalle);
                    setAddNuevo(false);
                } else {
                    alert_warning("El monto debe ser mayor a 0");
                }
            } else {
                alert_warning("Por favor complete la información solicitada");
            }
        } else {
            alert_warning("Por favor complete la información solicitada");
        }
    }

    useEffect(() => {
        if (Number(llaveId) > 0) {
            let item = listContraparte.find(i => Number(i.id) === Number(llaveId));
            setContraParteEdit(item);
            setValues({ ...values, contraparte: item });
        }
    }, [listContraparte]);

    const handleEditItemContraparte = () => {
        if (values) {
            let { id = 0, nombre } = values.contraparte;
            if (Number(id) > 0) {
                let { monto = "0" } = values;

                monto = String(monto).replace("Q ", "");
                monto = String(monto).replace(",", "");
                if (Number(monto) > 0) {
                    let itemDetalle = detalleContraparte.map(item => {
                        if (Number(item.llaveId) === Number(llaveId)) {
                            item.monto = monto;
                            item.nombre = nombre;
                            item.llaveId = id;
                            item.editar = false;
                            return item;
                        } else {
                            return item;
                        }
                    });
                    setDetalleContraparte(itemDetalle);
                    setAddNuevo(false);
                    setEditarItem(false);
                } else {
                    alert_warning("El monto debe ser mayor a 0");
                }
            } else {
                alert_warning("Por favor complete la información solicitada");
            }
        } else {
            alert_warning("Por favor complete la información solicitada");
        }
    }

    return (
        <tr>
            <td></td>
            <td>
                <div className="mb-3 input-group">
                    {
                        <>
                            <Autocomplete
                                value={contraParteEdit || null}
                                id="items_contraparte"
                                name="items_contraparte"
                                options={listContraparte || []}
                                getOptionLabel={(option) => option.nombre}
                                style={{ width: 600 }}
                                size="small"
                                onChange={onchangeCuenta}
                                renderInput={(params) => <TextField {...params} label="Contraparte" />}
                            />
                        </>
                    }

                </div>
            </td>
            <td>
                <NumberFormat
                    id="monto"
                    name="monto"
                    className="form-control"
                    thousandSeparator prefix="Q "
                    value={values.monto}
                    onChange={handleOnChange}
                    readOnly={bloquearHaber}
                    onKeyPress={handleKeyPress} />
            </td>
            <td>
                {
                    editarItem === false ?
                        <button
                            type="button"
                            className="btn-icon btn btn-success btn-sm"
                            onClick={() => { addItemContraparte() }}>
                            <i className="feather icon-plus" />
                        </button>
                        : <button
                            type="button"
                            className="btn-icon btn btn-primary btn-sm"
                            onClick={() => { handleEditItemContraparte() }}>
                            <i className="feather icon-edit" />
                        </button>
                }

            </td>
        </tr>
    )
}
