import React, { useState, useEffect, useRef } from 'react'
import { Modal, Row, Card, Button, Col, } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import TreeView from 'deni-react-treeview';
import callApi from '../../../helpers/conectorApi';
import Loading from '../base/Loading'
const menuId = 40;
const menuIdCuentaContable = 41;
export const GrupoContable = ({ abrirModal, setAbrirModal, listCuentas=[],handleSetCuenta }) => {
    const [loading, setLoading] = useState(false);
    const state = useSelector(state => state);
    const [accesos, setAccesos] = useState([]);
    const [catGrupoContable, setCatGrupoContable] = useState([]);
    const [dataArbol, setDataArbol] = useState([]);
    const [armarArbol, setArmarArbol] = useState(false);
    const treeviewRef = useRef(null);
    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuId || item.menuId === menuIdCuentaContable);
            setAccesos(misAccesos);
        }
        setLoading(false);
    }

    const onSelectItemHandler = (item) => {
        const api = treeviewRef.current?.api;
        // const parent = api.getParentNode(item);
    };

    const GetCuentas = (grupo_contableId) => {
        let cuentasAux = [];
        let gruposAux = [];
        cuentasAux = listCuentas.filter(i => Number(i.grupo_contableId) === Number(grupo_contableId));
        gruposAux = catGrupoContable.filter(i => Number(i.padre_grupo_contableId) === Number(grupo_contableId));

        if (cuentasAux) {
            let cuentas = [];
            cuentasAux.map(({ cuenta_contableId, codigo, nombre }) => {
                cuentas.push({
                    id: cuenta_contableId,
                    text: `${codigo} - ${nombre}`,
                    isLeaf: true
                });
            });

            gruposAux.map(({ grupo_contableId, codigo, descripcion, padre_grupo_contableId }) => {
                cuentas.push({
                    id: codigo,
                    text: `${codigo} - ${descripcion}`,
                    children: GetCuentas(grupo_contableId)
                });
            });

            return cuentas;
        }

    }

    const armarDatArbol = () => {
        let padres = [];
        padres = catGrupoContable.filter(i => i.padre_grupo_contableId === null)

        let listArbol = [];
        if (padres) {
            padres.map(({ grupo_contableId, codigo, descripcion, padre_grupo_contableId }) => {
                listArbol.push({
                    id: codigo,
                    text: `${codigo} - ${descripcion}`,
                    children: GetCuentas(grupo_contableId)
                });
            });
        }
        setDataArbol(listArbol);
    }
    const GetGrupoContable = async () => {
        if (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3)) {
            setLoading(true);
            let responseGrupo = await callApi(`grupocontable?include=0&estadoId=1`);
            if (responseGrupo) {
                setCatGrupoContable(responseGrupo);
                setArmarArbol(true);
            }
            setLoading(false);
        } else {
            setCatGrupoContable([{ nombre: 'No esta autorizado' }]);
        }
    }

       useEffect(() => {
        GetAccesosByMenuId();
    }, [])

    useEffect(() => {
        GetGrupoContable();
    }, [accesos])

    useEffect(() => {
        armarDatArbol();
    }, [armarArbol])


    const actionButtons = [
        <Button
            type="button"
            name="btnSeleccionarCuenta"
            className="btn-icon-files"
            variant="outline-primary"
            size="sm"
        >
            <i className="feather icon-edit" />
        </Button>
    ];

    const onActionButtonClick = (item, actionButton) => {
        const buttonName = actionButton.props.name;
        switch (buttonName) {
            case "btnSeleccionarCuenta":
                const api = treeviewRef.current?.api;
                const parent = api.getParentNode(item);
                handleSetCuenta(item);
                break;
            default:
                break;
        }
    };

    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)} size="xl" centered={true}>
            {
                loading === true ?
                    <Loading />
                    : <>
                        <Modal.Header closeButton>
                            <Modal.Title as="h5">Árbol de Cuentas</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <Row>
                                <Col>
                                    <TreeView
                                        ref={treeviewRef}
                                        className="customizable-tree"
                                        items={dataArbol}
                                        theme="orange"
                                        showRoot={false}
                                        showCheckbox={false}
                                        marginItems="40"
                                        selectRow={true}
                                        onSelectItem={onSelectItemHandler}
                                        actionButtons={actionButtons}
                                        onActionButtonClick={onActionButtonClick} />
                                </Col>
                            </Row>
                        </Modal.Body>

                    </>
            }
        </Modal>
    )
}