import React, { useState, useEffect } from 'react'
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import NumberFormat from 'react-number-format'
import { useForm } from '../../hooks/useForm';
import { alert_warning } from '../../../helpers/Notificacion';
import { GrupoContable } from './GrupoContable';
import { ContraparteInsert } from './ContraparteInsert';
export const AsientoContable = ({ listCuentas, debe, haber, idCuenta = 0, setAsientos, asientos, setAddNuevo, editarItem = false, setEditarItem, setModalContraparte }) => {
    const [bloquearDebe, setBloquearDebe] = useState(false);
    const [abrirModal, setAbrirModal] = useState(false);
    const [btnHabilitarBusqueda, setBtnHabilitarBusqueda] = useState(false);
    const [nombreDeCuenta, setNombreDeCuenta] = useState('');
    const [bloquearHaber, setBloquearHaber] = useState(false);
    const [cuentaEdit, setCuentaEdit] = useState(null);
    const [values, handleOnChange, , setValues] = useForm({
        cuenta: 0,
        debe,
        haber
    });

    const onchangeCuenta = (event, nuevoValor) => {
        setValues({ ...values, cuenta: nuevoValor });
        setCuentaEdit(nuevoValor);
    }
    const handleKeyPress = (event) => {
        if (event.key === 'Enter') {
            if (idCuenta > 0) {
                handleEditAsiento();
            } else {
                addAsiento();
            }
        }
    }
    const addAsiento = () => {
        if (values) {
            if (validar(values)) {
                let { cuenta, debe, haber } = values;
                let { cuenta_contableId, nombre, tipo_contraparteId = 0 } = cuenta;
                let itemActuales = asientos;

                debe = String(debe).replace("Q ", "");
                debe = String(debe).replace(",", "");
                haber = String(haber).replace("Q ", "");
                haber = String(haber).replace(",", "");

                itemActuales.push({
                    cuenta_contableId,
                    debe: Number.parseFloat(debe),
                    haber: Number.parseFloat(haber),
                    nombreCuenta: nombre,
                    editar: false
                });
                setAsientos(itemActuales);
                setAddNuevo(false);
                if (tipo_contraparteId > 0) {
                    setModalContraparte(cuenta_contableId);
                }
            }
        } else {
            alert_warning("Por favor seleccione una cuenta contable");
        }
    }

    const handleEditAsiento = () => {
        if (values) {
            if (validar(values)) {

                let nuevo = [];
                let cuentaId = undefined;
                nuevo=asientos.map(item => {
                    if (Number(item.cuenta_contableId) === Number(idCuenta)) {
                        let itemCuenta = listCuentas.find(i => Number(i.cuenta_contableId) === Number(idCuenta));
                        let { cuenta, debe, haber } = values;
                        let { cuenta_contableId, nombre } = cuenta;
                        let itemActuales = asientos;

                        debe = String(debe).replace("Q ", "");
                        debe = String(debe).replace(",", "");
                        haber = String(haber).replace("Q ", "");
                        haber = String(haber).replace(",", "");

                        item.cuenta_contableId=cuenta_contableId;
                        item.debe= Number.parseFloat(debe);
                        item.haber=Number.parseFloat(haber);
                        item.nombreCuenta=nombre;
                        item.editar=false;

                        let { tipo_contraparteId = 0 } = itemCuenta;
                        if (tipo_contraparteId > 0) {
                            cuentaId = cuenta_contableId;
                        }
                        return item;

                    } else {
                        return item;
                    }


                });
                setAsientos(nuevo);
                setAddNuevo(false);
                setEditarItem(false);
                if (cuentaId) {
                    setModalContraparte(cuentaId);
                }
            }
        } else {
            alert_warning("Por favor seleccione una cuenta contable");
        }
    }

    const handleSetCuenta = ({ id }) => {
        const item = listCuentas.find(i => Number(i.cuenta_contableId) === Number(id));
        setValues({ ...values, cuenta: item });
        let { nombre } = item;
        setNombreDeCuenta(nombre);
        setAbrirModal(false);
        setBtnHabilitarBusqueda(true);
    }

    const handleAbrirModalCuentas = () => {
        setAbrirModal(true);
    }

    const handleLimpiarBtn = () => {
        setBtnHabilitarBusqueda(false);
        setNombreDeCuenta('');
    }

    const validar = (data) => {
        let { cuenta, debe, haber } = data;
        let { cuenta_contableId = 0, nombre } = cuenta;
        if (Number(cuenta_contableId) > 0) {
            debe = String(debe).replace("Q ", "");
            debe = String(debe).replace(",", "");
            haber = String(haber).replace("Q ", "");
            haber = String(haber).replace(",", "");
            if (Number(debe) > 0 || Number(haber) > 0) {
                return true;
            } else {
                alert_warning("Por favor ingrese un monto mayor a 0 en el DEBE o HABER");
                return false;
            }
        } else {
            alert_warning("Por favor seleccione una cuenta");
            return false;
        }
    }

    useEffect(() => {
        let { debe: valDebe, haber: valHaber } = values;
        if (valDebe) {
            valDebe = String(valDebe).replace("Q ", "");
            valDebe = String(valDebe).replace(",", "");
            if (Number(valDebe) === 0) {
                setBloquearHaber(false);
            } else {
                setBloquearHaber(true);
            }
        }
        if (valHaber) {
            valHaber = String(valHaber).replace("Q ", "");
            valHaber = String(valHaber).replace(",", "");
            if (Number(valHaber) === 0) {
                setBloquearDebe(false);
            } else {
                setBloquearDebe(true);
            }
        }
    }, [values]);


    useEffect(() => {
        if (Number(idCuenta) > 0) {
            let item = listCuentas.find(i => Number(i.cuenta_contableId) === Number(idCuenta));
            setCuentaEdit(item);
            setValues({ ...values, cuenta: item });
        }
    }, [listCuentas]);
    return (
        <tr>
            <td></td>
            <td>
                <div className="mb-3 input-group">
                    {
                        (listCuentas.length > 0) &&
                        <>
                            {
                                btnHabilitarBusqueda === true ?
                                    <input type="text" className="form-control" readOnly={true} value={nombreDeCuenta}></input>
                                    :
                                    <Autocomplete
                                        value={cuentaEdit || null}
                                        id="cuenta_contableId"
                                        name="cuenta_contableId"
                                        options={listCuentas || []}
                                        getOptionLabel={(option) => option.nombre}
                                        style={{ width: 600 }}
                                        size="small"
                                        onChange={onchangeCuenta}
                                        renderInput={(params) => <TextField {...params} label="Nombre de la cuenta" />}
                                    />
                            }
                            <div className="input-group-append">
                                {
                                    btnHabilitarBusqueda === true ?
                                        <button
                                            type="button"
                                            className="btn btn-danger btn-sm"
                                            onClick={() => handleLimpiarBtn()}>
                                            <i className="feather icon-trash-2" />
                                        </button>
                                        :
                                        <button
                                            type="button"
                                            className="btn btn-primary btn-sm"
                                            onClick={() => handleAbrirModalCuentas()}>
                                            <i className="feather icon-search" />
                                        </button>
                                }
                            </div>
                        </>
                    }

                </div>
            </td>
            <td>
                <NumberFormat
                    id="debe"
                    name="debe"
                    className="form-control"
                    thousandSeparator prefix="Q "
                    value={values.debe}
                    onChange={handleOnChange}
                    readOnly={bloquearDebe}
                    onKeyPress={handleKeyPress}
                />
            </td>
            <td>
                <NumberFormat
                    id="haber"
                    name="haber"
                    className="form-control"
                    thousandSeparator prefix="Q "
                    value={values.haber}
                    onChange={handleOnChange}
                    readOnly={bloquearHaber}
                    onKeyPress={handleKeyPress} />
            </td>
            <td>
                {
                    editarItem === false ?
                        <button
                            type="button"
                            className="btn-icon btn btn-success btn-sm"
                            onClick={() => { addAsiento() }}>
                            <i className="feather icon-plus" />
                        </button>
                        : <button
                            type="button"
                            className="btn-icon btn btn-primary btn-sm"
                            onClick={() => { handleEditAsiento() }}>
                            <i className="feather icon-edit" />
                        </button>
                }

            </td>
            {
                (abrirModal === true && listCuentas.length > 0) &&
                <GrupoContable abrirModal={abrirModal} setAbrirModal={setAbrirModal} listCuentas={listCuentas} handleSetCuenta={handleSetCuenta} />
            }

        </tr>
    )
}
