import moment from 'moment';
import React, { useState, useEffect } from 'react'
import { Table } from 'react-bootstrap'
import DatePicker from "react-datepicker";
import { useDispatch, useSelector } from 'react-redux';
import { Form } from 'react-bootstrap'
import { Card, Col } from 'react-bootstrap'
import { Row } from 'react-bootstrap'
import { TextInput, ValidationForm } from 'react-bootstrap4-form-validation'
import NumberFormat from 'react-number-format'
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion'
import Aux from '../../../hoc/_Aux'
import { useForm } from '../../hooks/useForm'
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import callApi from '../../../helpers/conectorApi';
import { AsientoContable } from './AsientoContable';
import Loading from '../base/Loading';
import { ContraparteInsert } from './ContraparteInsert';
import { VistaPreviaPartida } from './VistaPreviaPartida';

const menuIdCuentaContable = 41;
const menuId = 42;
export const PartidaContable = ({setNuevaPartida,handleVistaPrevia}) => {
    const state = useSelector(state => state);
    const [catCuentasContables, setCatCuentasContables] = useState([]);
    const [listCuentas, setListCuentas] = useState([]);
    const [addNuevo, setAddNuevo] = useState(true);
    const [editarItem, setEditarItem] = useState(false);
    const [asientos, setAsientos] = useState([]);
    const [loading, setLoading] = useState(true)
    const [accesos, setAccesos] = useState([]);
    const [partidaValida, setPartidaValida] = useState(false)
    const [fechaContable, setFechaContable] = useState('');
    const [modalContraparte, setModalContraparte] = useState(false);
    const [codigoCuenta, setCodigoCuenta] = useState(0);
    const [itemCuenta, setItemCuenta] = useState({});
    const [sumas, setSumas] = useState({
        debe: 0,
        haber: 0
    });
    const [values, handleOnChange, , setValues] = useForm({
        observaciones: '',
        fecha: undefined
    });  
    
    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuId || item.menuId === menuIdCuentaContable);
            setAccesos(misAccesos);
        }
        setLoading(false);
    }
    const GetCuentasContables = async () => {
        if (accesos.find(acceso => acceso.menuId === menuIdCuentaContable && acceso.accesoId === 3)) {
            setLoading(true);
            let response = await callApi(`cuentacontable?include=0&estado=1`);
            if (response) {
                setCatCuentasContables(response);
                setListCuentas(response);
            }
            setLoading(false);
        } else {
            setCatCuentasContables([{ nombre: 'No esta autorizado' }]);
            setListCuentas([{ nombre: 'No esta autorizado' }]);
        }
    }


    const handleGetContraparte = (cuenta_contableId) => {
        setCodigoCuenta(cuenta_contableId);
    }

    const auxSetMonto = () => {
        let item = asientos.find(i => Number(i.cuenta_contableId) === Number(codigoCuenta));
        let infoCuenta = catCuentasContables.find(i => Number(i.cuenta_contableId) === Number(codigoCuenta));
        if (infoCuenta && item) {
            let info = {
                movimiento: item,
                infoCuenta: infoCuenta
            }
            setItemCuenta(info);
            setModalContraparte(true);
        } else {
            alert_warning("Ocurrió un error al obtener la información de la cuenta");
        }
    }

    const handleOnSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        let data = {};
        let { observaciones, fecha } = values;
        data.observaciones = observaciones;
        data.fecha = fecha;
        let detalle = [];
        for await (let { cuenta_contableId, debe, haber, contraparte } of asientos) {
            let monto = 0;
            let existeContraparte = false;
            let contraParteAux = {};
            let datelleContraparte = [];
            if (Number(debe) > 0) {
                monto = debe;
                if (contraparte) {
                    contraParteAux.tipo_contraparteId = contraparte.tipo_contraparteId;
                    if (contraparte.detalle) {
                        existeContraparte = true;
                        contraparte.detalle.map(({ llaveId, monto: montoContraparte }) => {
                            datelleContraparte.push({
                                llaveId,
                                monto: Number(montoContraparte)
                            });
                        });
                    } else {
                        alert_warning("Favor de completar la contraparte");
                        return false;
                    }
                    contraParteAux.detalle = datelleContraparte;
                }
            }
            if (Number(haber) > 0) {
                monto = -haber;
                if (contraparte) {
                    contraParteAux.tipo_contraparteId = contraparte.tipo_contraparteId;
                    if (contraparte.detalle) {
                        existeContraparte = true;
                        contraparte.detalle.map(({ llaveId, monto: montoContraparte }) => {
                            let auxMontoContraparte = -montoContraparte;
                            datelleContraparte.push({
                                llaveId,
                                monto: Number(auxMontoContraparte)
                            });
                        });
                    } else {
                        alert_warning("Favor de completar la contraparte");
                        return false;
                    }
                    contraParteAux.detalle = datelleContraparte;
                }

            }
            if (existeContraparte === true) {
                let item = {
                    cuenta_contableId,
                    monto: Number.parseFloat(monto),
                    contraparte: contraParteAux
                }
                detalle.push(item);
            } else {
                let item = {
                    cuenta_contableId,
                    monto: Number.parseFloat(monto)
                }
                detalle.push(item);
            }

        }
        data.detalle = detalle;

        let response = await callApi('partidacontable', {
            method: 'POST',
            body: JSON.stringify(data)
        });
        if (response) {
            alert_exitoso("Partida contable registrada exitosamente");
            setNuevaPartida(false);
            handleVistaPrevia(response);
            //setPartida(response);

        }
        setLoading(false);
    }

    useEffect(() => {
        GetAccesosByMenuId();
    }, []);


    const handleSetFecha = (fecha) => {
        const fechaFormat = moment(fecha).format('YYYY/MM/DD');
        setValues({ ...values, fecha: fechaFormat });
        setFechaContable(fecha);
    }

    useEffect(() => {
        GetCuentasContables();
    }, [accesos])

    const handleErrorSubmit = (e, formData, errorInputs) => {
        alert_warning("Por favor complete toda la información solicitada por el formulario");
    };

    useEffect(() => {
        let infoSumas = {};
        let sumDebe = 0, sumHaber = 0;
        asientos.map(({ debe, haber }) => {
            sumDebe += Number.parseFloat(debe);
            sumHaber += Number.parseFloat(haber);
        });
        infoSumas.debe = Number.parseFloat(sumDebe);
        infoSumas.haber = Number.parseFloat(sumHaber);
        setSumas(infoSumas);
        excluirCuentas();
    }, [addNuevo, asientos]);

    const excluirCuentas = () => {
        let nuevaLista = [];
        catCuentasContables.map(item => {
            let existe = asientos.find(i => Number(i.cuenta_contableId) === Number(item.cuenta_contableId) && i.editar === false);
            if (!existe) {
                nuevaLista.push(item);
            }
        });
        setListCuentas(nuevaLista);
    }

    useEffect(() => {
        let { debe, haber } = sumas;

        if ((Number(debe) > 0 || Number(haber) > 0) && Math.abs(Number(debe).toFixed(2)) === Math.abs(Number(haber).toFixed(2))) {
            setPartidaValida(true);
        } else {
            setPartidaValida(false);
        }

    }, [sumas])

    const eliminarFila = (id) => {
        let nuevos = asientos.filter(i => Number(i.cuenta_contableId) !== Number(id))
        setAsientos(nuevos);
    }

    const handleEditar = (cuenta_contableId) => {
        let actuales = asientos;
        actuales = asientos.map(item => {
            if (item.cuenta_contableId === cuenta_contableId) {
                item.editar = true;
                return item;
            } else {
                return item;
            }
        });
        setEditarItem(true);
        setAsientos(actuales);
        setCodigoCuenta(0);
        setAddNuevo(false);
    }

    useEffect(() => {
        if (Number(codigoCuenta) > 0) {
            auxSetMonto();
        }
    }, [codigoCuenta])

    function addDays(date, days) {
        var result = new Date(date);
        result.setDate(result.getDate() + days);
        return result;
    }
    const errorMessage = "Campo obligatorio";

    
    return (
        <Aux>
            <Row>
                <Col>
                    <ValidationForm onSubmit={handleOnSubmit} onErrorSubmit={handleErrorSubmit}>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">Registrar Partida Contable</Card.Title>
                                <span className="d-block m-t-5">Por favor realice la partida en papel antes de realizarla en la aplicación.</span>
                                <Row className="align-items-center m-l-0">
                                    <Col />
                                    <Col className="text-right">
                                        {
                                            <>
                                            <button type="button" className="btn btn-warning mr-1" onClick={()=>{setNuevaPartida(false);}}><i className="feather icon-back" />Cancelar</button>
                                            <button type="submit" className="btn btn-success" disabled={!partidaValida}><i className="feather icon-plus" />Registrar</button>
                                            </>
                                        }
                                    </Col>
                                </Row>
                            </Card.Header>
                            <Card.Body>

                                {
                                    loading === true ?
                                        <Loading />
                                        :<>
                                            <Form.Row>
                                                <Form.Group as={Col} md="12">
                                                    <Form.Label htmlFor="observaciones">Observaciones</Form.Label>
                                                    <TextInput
                                                        name="observaciones"
                                                        id="observaciones"
                                                        required
                                                        multiline
                                                        value={values.observaciones}
                                                        onChange={handleOnChange}
                                                        errorMessage={errorMessage}
                                                        placeholder="Observaciones"
                                                        autoComplete="off"
                                                        type="text"
                                                        rows="2"
                                                        minLength="10"
                                                    />
                                                </Form.Group>
                                                <Form.Group as={Col} md="12">
                                                    <Form.Label htmlFor="fecha_nacimiento">Fecha</Form.Label>
                                                    <div className="form-group">
                                                        <DatePicker
                                                            id="fecha_nacimiento"
                                                            name="fecha_nacimiento"
                                                            dateFormat="dd/MM/yyyy"
                                                            selected={fechaContable}
                                                            onChange={handleSetFecha}
                                                            required
                                                            errorMessage={errorMessage}
                                                            className="form-control"
                                                            placeholderText="dd/MM/yyyy"
                                                            autoComplete="off"
                                                            maxDate={new Date()}
                                                            minDate={addDays(new Date(), -5)}
                                                        />
                                                    </div>
                                                </Form.Group>

                                            </Form.Row>
                                            <Table responsive>
                                                <thead>
                                                    <tr>
                                                        <th>Línea</th>
                                                        <th>Cuenta</th>
                                                        <th>Debe Q</th>
                                                        <th>Haber Q</th>
                                                        <th></th>
                                                    </tr>
                                                </thead>
                                                <tbody>

                                                    {
                                                        asientos.map((item, index) => {
                                                            let { editar = false, cuenta_contableId } = item;

                                                            return (
                                                                editar === false ?
                                                                    <tr key={index}>
                                                                        <td>{index + 1}</td>
                                                                        <td>{item.nombreCuenta}</td>
                                                                        <td>Q {Number.parseFloat(item.debe).toFixed(2)}</td>
                                                                        <td>Q {Number.parseFloat(item.haber).toFixed(2)}</td>
                                                                        <td>
                                                                            {
                                                                                !editarItem &&
                                                                                <button
                                                                                    type="button"
                                                                                    className="btn btn-info btn-sm mr-1"
                                                                                    onClick={() => { handleEditar(item.cuenta_contableId) }}>
                                                                                    <i className="feather icon-edit" />
                                                                                </button>
                                                                            }
                                                                            <button
                                                                                className="btn btn-danger btn-sm"
                                                                                onClick={() => { eliminarFila(item.cuenta_contableId) }}
                                                                                type="button">
                                                                                <i className="feather icon-trash-2" />
                                                                            </button>
                                                                        </td>
                                                                    </tr>
                                                                    : <AsientoContable
                                                                        listCuentas={listCuentas}
                                                                        key={100}
                                                                        idCuenta={cuenta_contableId}
                                                                        debe={item.debe}
                                                                        haber={item.haber}
                                                                        setAsientos={setAsientos}
                                                                        asientos={asientos}
                                                                        setAddNuevo={setAddNuevo}
                                                                        editarItem={true}
                                                                        setEditarItem={setEditarItem}
                                                                        setModalContraparte={handleGetContraparte}
                                                                    />
                                                            )
                                                        })
                                                    }
                                                    {
                                                        (catCuentasContables.length > 0 && addNuevo === true) &&
                                                        <AsientoContable
                                                            listCuentas={listCuentas}
                                                            key={100}
                                                            edit={true}
                                                            debe={0}
                                                            haber={0}
                                                            setAsientos={setAsientos}
                                                            asientos={asientos}
                                                            setAddNuevo={setAddNuevo}
                                                            setModalContraparte={handleGetContraparte}
                                                        />
                                                    }
                                                </tbody>
                                                <tfoot>
                                                    <tr>
                                                        <td colSpan="2">Total</td>
                                                        <td>Q {Number.parseFloat(sumas.debe).toFixed(2)}</td>
                                                        <td>Q {Number.parseFloat(sumas.haber).toFixed(2)}</td>
                                                    </tr>
                                                </tfoot>
                                            </Table>
                                            {
                                                !addNuevo && !editarItem &&
                                                <Row className="align-items-center m-l-0">
                                                    <Col className="text-left">
                                                        {
                                                            <button type="button" className="btn btn-primary btn-sm" onClick={() => { setAddNuevo(true) }}><i className="feather icon-plus" />Nueva Fila</button>
                                                        }
                                                    </Col>
                                                    <Col />
                                                </Row>
                                            }

                                            {
                                                modalContraparte === true &&
                                                <ContraparteInsert
                                                    abrirModal={modalContraparte}
                                                    setAbrirModal={setModalContraparte}
                                                    setAsientos={setAsientos}
                                                    {...itemCuenta}
                                                    asientos={asientos}
                                                />
                                            }
                                        </>
                                }
                            </Card.Body>
                        </Card>
                    </ValidationForm>
                </Col>
            </Row>
        </Aux>
    )
}