import { savePDF } from '@progress/kendo-react-pdf';

class DocService {
    createPdf = (html) => {
        savePDF(html, {
           // paperSize: 'Letter',
            paperSize: "auto",
            fileName: 'Reporte.pdf',
            margin: 40,
            landscape:false,
            scale:0.5
        })
    }
}

const Doc = new DocService();
export default Doc;