import React, { useEffect, useState } from 'react'
import { Row, Col, Card, Button, Table } from 'react-bootstrap';
import moment from 'moment';
import { useSelector } from 'react-redux';
import callApi from '../../../helpers/conectorApi';
import Aux from '../../../hoc/_Aux';
import { NoAutorizado } from '../base/NoAutorizado';
import Loading from '../base/Loading';
import { PartidaContable } from './PartidaContable';
import { VistaPreviaPartida } from './VistaPreviaPartida';
import { asignarEstiloTabla, limpiarEstiloTabla } from '../../../helpers/estiloTabla';
import { useForm } from '../../hooks/useForm';
const menuId = 42;
export const PartidasListar = () => {
    const state = useSelector(state => state);
    const [loading, setLoading] = useState(true)
    const [accesos, setAccesos] = useState([]);
    const [abrirModal, setAbrirModal] = useState(false);
    const [nuevaPartida, setNuevaPartida] = useState(false);
    const [partidasContables, setPartidasContables] = useState([]);
    const [partida, setPartida] = useState({});
    const [values, handleOnChange, , setValues] = useForm({
        fechaInicial: moment(new Date(new Date().getFullYear(), new Date().getMonth(), 1)).format('YYYY-MM-DD'),
        fechaFinal: moment(new Date()).format('YYYY-MM-DD')
    });
    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuId);
            setAccesos(misAccesos);
        }
        setLoading(false);
    }

    const handleFiltrar = async () => {
        limpiarEstiloTabla("#tablePartidas");
        if (accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3)) {
            setLoading(true);
            let response = await callApi('partidacontable/filtrar', {
                method: 'POST',
                body: JSON.stringify(values)
            });
            if (response) {
                setPartidasContables(response);
                asignarEstiloTabla("#tablePartidas", 25);
            }
            setLoading(false);
        }
    }
    const handleVistaPrevia = (infoPartida) => {
        setPartida(infoPartida);
        setAbrirModal(true);
    }

    useEffect(() => {
        GetAccesosByMenuId();
    }, [state?.accesos]);

    useEffect(() => {
        handleFiltrar();
    }, [accesos]);

    useEffect(() => {
        handleFiltrar();
    }, [nuevaPartida]);

    return (
        accesos.length > 0 &&
            nuevaPartida === false ?
            <Aux>
                <Row className='btn-page'>
                    <Col sm={12}>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">Comprobantes Contables</Card.Title>
                            </Card.Header>
                            <Card.Body>
                                {
                                    loading === true ?
                                        <Loading />
                                        : <>
                                            <Row className="align-items-center m-l-0">
                                                <Col />
                                                <Col className="text-right">
                                                    {
                                                        accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 1) &&
                                                        <Button variant="success" className="btn-round has-ripple" onClick={() => { setNuevaPartida(true); }}><i className="feather icon-plus" /> Nuevo</Button>
                                                    }
                                                </Col>
                                            </Row>
                                            {
                                                accesos.find(acceso => acceso.menuId === menuId && acceso.accesoId === 3) ?
                                                    <>
                                                        <Card>
                                                            <Card.Header>
                                                                <h5>Filtros</h5>
                                                            </Card.Header>
                                                            <Card.Body className='pb-0'>
                                                                <div className="row">
                                                                    <div className="col-md-3">
                                                                        <div className="form-group col">
                                                                            <label className="form-label">Fecha Inicial</label>
                                                                            <input
                                                                                type="date"
                                                                                id="fechaInicial"
                                                                                name="fechaInicial"
                                                                                value={values.fechaInicial}
                                                                                onChange={handleOnChange}
                                                                                className="form-control" />
                                                                        </div>
                                                                    </div>
                                                                    <div className="col-md-3">
                                                                        <div className="form-group col">
                                                                            <label className="form-label">Fecha Final</label>
                                                                            <input
                                                                                type="date"
                                                                                id="fechaFinal"
                                                                                name="fechaFinal"
                                                                                value={values.fechaFinal}
                                                                                onChange={handleOnChange}
                                                                                className="form-control" />
                                                                        </div>
                                                                    </div>
                                                                    <div className="col-md-3">
                                                                        <div className="form-group col">
                                                                            <label className="form-label">&nbsp;&nbsp;</label>
                                                                            <div className="mb-3 input-group">
                                                                                <div className="input-group-append">
                                                                                    <button className="btn btn-primary" onClick={() => { handleFiltrar() }}>Filtrar<i className="feather icon-search" /></button>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                    </div>

                                                                </div>
                                                            </Card.Body>
                                                        </Card>

                                                        <Card>
                                                            <Card.Header>
                                                                <h5>Documentos</h5>
                                                            </Card.Header>
                                                            <Card.Body className='pb-0'>
                                                                <Table striped hover responsive bordered id="tablePartidas">
                                                                    <thead>
                                                                        <tr>
                                                                            <th>Código</th>
                                                                            <th>Periodo</th>
                                                                            <th>No. Partida</th>
                                                                            <th>Fecha</th>
                                                                            <th>Monto</th>
                                                                            <th>Usuario</th>
                                                                            <th>Fecha Ingreso</th>
                                                                            <th style={{ textAlign: "center" }}>Visualizar</th>
                                                                        </tr>
                                                                    </thead>
                                                                    <tbody>
                                                                        {
                                                                            partidasContables.map((item) => {
                                                                                let { partida_contableId, numero_partida, observaciones, fecha, fecha_crea, Usuario, Periodo, Detalle } = item;
                                                                                let monto = 0;
                                                                                monto = Detalle.reduce((prev, cur) => Number(prev) + Number(cur.monto), 0);
                                                                                return (
                                                                                    <tr key={partida_contableId}>
                                                                                        <td>{partida_contableId}</td>
                                                                                        <td>{Periodo.descripcion}</td>
                                                                                        <td>{numero_partida}</td>
                                                                                        <td>{moment(fecha).format('DD/MM/YYYY') || ""}</td>
                                                                                        <td>Q{Number(monto).toFixed(2)}</td>
                                                                                        <td>{Usuario.user_name}</td>
                                                                                        <td>{moment(fecha_crea).format('DD/MM/YYYY') || ""}</td>
                                                                                        <td style={{ textAlign: "center" }}><button className="btn-icon btn btn-info btn-sm" onClick={() => { handleVistaPrevia(item); }}><i className="feather icon-eye" /></button></td>
                                                                                    </tr>
                                                                                )
                                                                            })
                                                                        }
                                                                    </tbody>
                                                                </Table>

                                                            </Card.Body>
                                                        </Card>
                                                    </>
                                                    : <NoAutorizado />
                                            }
                                        </>
                                }
                                {
                                    (abrirModal === true && partida) &&
                                    <VistaPreviaPartida
                                        abrirModal={abrirModal}
                                        setAbrirModal={setAbrirModal}
                                        partida={partida}
                                    />
                                }
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
            : <PartidaContable setNuevaPartida={setNuevaPartida} handleVistaPrevia={handleVistaPrevia} />
    )
}
