import { Modal, Row, Card, Button, Col, } from 'react-bootstrap';
import React, { useState, useEffect } from 'react'
import { Form } from 'react-bootstrap'
import { ValidationForm } from 'react-bootstrap4-form-validation'
import { alert_warning } from '../../../helpers/Notificacion'
import Loading from '../base/Loading'
import { Table } from 'react-bootstrap';
import { FilaItemContraparte } from './FilaItemContraparte';
import callApi from '../../../helpers/conectorApi';

export const ContraparteInsert = ({ abrirModal, setAbrirModal, movimiento, infoCuenta, asientos, setAsientos }) => {
    const [loading, setLoading] = useState(false);
    const [detalleContraparte, setDetalleContraparte] = useState([]);
    const [addNuevo, setAddNuevo] = useState(true);
    const [editarItem, setEditarItem] = useState(false);
    const [sumaContraparte, setSumaContraparte] = useState(0);
    const [montoMovimiento, setMontoMovimiento] = useState(0);
    const [listContraparte, setListContraparte] = useState([]);
    const [listContraparteAux, setListContraparteAux] = useState([]);


    const handleConfirmar = () => {
        if (asientos) {
            if (Math.abs(Number(sumaContraparte).toFixed(2)) === Math.abs(Number(montoMovimiento).toFixed(2))) {
                let items = asientos.map(item => {
                    if (Number(item.cuenta_contableId) === Number(infoCuenta.cuenta_contableId)) {
                        let auxContraparte = {};
                        auxContraparte.tipo_contraparteId = infoCuenta.tipo_contraparteId;
                        auxContraparte.detalle = detalleContraparte;
                        item.contraparte = auxContraparte;
                        return item;
                    } else {
                        return item;
                    }
                });
                setAsientos(items);
                setAbrirModal(false);
            } else {
                alert_warning("Para poder confirmar la información de contraparte la sumatoria de todas las filas debe ser igual a " + Number(montoMovimiento).toFixed(2))
            }
        } else {
            alert_warning("Ocurrió un error al intentar leer las filas de la partida")
        }
    }

    const handleEliminarFila = (id) => {
        let itemsActuales = detalleContraparte.filter(i => Number(i.llaveId) !== Number(id));
        setDetalleContraparte(itemsActuales);
    }

    const GetListContraparte = async () => {
        let response = await callApi(`partidacontable/listcontraparte/${infoCuenta.tipo_contraparteId}`);
        if (response) {
            setListContraparte(response);
            setListContraparteAux(response);
        }
    }

    const excluirContraparte = () => {
        let nuevaLista = [];
        listContraparteAux.map(item => {
            let existe = detalleContraparte.find(i => Number(i.llaveId) === Number(item.id) && i.editar === false);
            if (!existe) {
                nuevaLista.push(item);
            }
        });
        setListContraparte(nuevaLista);
    }

    const handleEditar = (llaveId) => {
        let actuales = detalleContraparte;
        actuales = detalleContraparte.map(item => {
            if (item.llaveId === llaveId) {
                item.editar = true;
                return item;
            } else {
                return item;
            }
        });
        setEditarItem(true);
        setAddNuevo(false);
        setDetalleContraparte(actuales);
    }

    useEffect(() => {
        let { contraparte = undefined } = movimiento;
        if (contraparte) {
            let { detalle = [],tipo_contraparteId=0 } = contraparte;
            if(Number(tipo_contraparteId)===Number(infoCuenta.tipo_contraparteId)){
            if(detalle){
                setAddNuevo(false);
            }
            let totalContraparte = detalle.reduce((prev, cur) => Number(prev) + Number(cur.monto), 0);
            setSumaContraparte(totalContraparte);
            setDetalleContraparte(detalle);
        }

        }
    }, [detalleContraparte]);

    useEffect(() => {
        let totalContraparte = detalleContraparte.reduce((prev, cur) => Number(prev) + Number(cur.monto), 0);
        setSumaContraparte(totalContraparte);
        excluirContraparte();
    }, [addNuevo, detalleContraparte])


    useEffect(() => {
        GetListContraparte();
        let { debe, haber } = movimiento;
        if (Number(debe) > 0) {
            setMontoMovimiento(debe);
        } else if (Number(haber) > 0) {
            setMontoMovimiento(haber);
        } else {
            alert_warning("Existe un error en los montos, por favor verifique")
        }

    }, []);

    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(true)} size="xl">
            {
                loading === true ?
                    <Loading />
                    : <>
                        <Modal.Header closeButton>
                            <Modal.Title as="h5">Contraparte de la cuenta {infoCuenta.nombre}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <label className="text-danger">El monto debe cuadrar con esta cantidad Q {Number(montoMovimiento).toFixed(2)}</label>
                            <Table striped hover responsive bordered id="mytable">
                                <thead>
                                    <tr>
                                        <th>Fila</th>
                                        <th>Nombre</th>
                                        <th>Monto</th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        detalleContraparte.map(({ llaveId, monto, nombre, editar = false }, index) => {
                                            return (
                                                editar === false ?
                                                    <tr key={index}>
                                                        <td>{index + 1}</td>
                                                        <td>{nombre}</td>
                                                        <td>Q {Number.parseFloat(monto).toFixed(2)}</td>
                                                        <th>{
                                                            !editarItem &&
                                                            <button
                                                                type="button"
                                                                className="btn btn-info btn-sm mr-1"
                                                                onClick={() => { handleEditar(llaveId) }}
                                                            >
                                                                <i className="feather icon-edit" />
                                                            </button>
                                                        }
                                                            <button
                                                                className="btn btn-danger btn-sm"
                                                                type="button"
                                                                onClick={() => { handleEliminarFila(llaveId) }}>
                                                                <i className="feather icon-trash-2" />
                                                            </button></th>
                                                    </tr>
                                                    : <FilaItemContraparte key={100}
                                                        {...infoCuenta}
                                                        setAsientos={setAsientos}
                                                        asientos={asientos}
                                                        movimiento={movimiento}
                                                        setDetalleContraparte={setDetalleContraparte}
                                                        detalleContraparte={detalleContraparte}
                                                        listContraparte={listContraparte}
                                                        setAddNuevo={setAddNuevo}
                                                        editarItem={true}
                                                        monto={monto}
                                                        llaveId={llaveId}
                                                        setEditarItem={setEditarItem}
                                                    />
                                            )
                                        })
                                    }

                                    {
                                        addNuevo === true &&
                                        <FilaItemContraparte
                                            {...infoCuenta}
                                            setAsientos={setAsientos}
                                            asientos={asientos}
                                            movimiento={movimiento}
                                            setDetalleContraparte={setDetalleContraparte}
                                            detalleContraparte={detalleContraparte}
                                            listContraparte={listContraparte}
                                            setAddNuevo={setAddNuevo}
                                        />
                                    }
                                </tbody>
                                <tfoot>
                                    <tr>
                                        <td colSpan="2">
                                            {
                                                !addNuevo && !editarItem &&
                                                <button type="button" className="btn btn-primary btn-sm" onClick={() => { setAddNuevo(true) }}><i className="feather icon-plus" />Nueva Fila</button>
                                            }
                                            <label className="text-right">&nbsp;&nbsp;&nbsp;Total</label>
                                        </td>
                                        <td colSpan="2">Q {Number(sumaContraparte).toFixed(2)}</td>
                                    </tr>
                                </tfoot>
                            </Table>
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="danger" onClick={() => { alert_warning("Por favor complete la información solicitada") }}>Cancelar</Button>
                            <Button variant="success" onClick={handleConfirmar}>Confirmar</Button>
                        </Modal.Footer>
                    </>
            }
        </Modal>
    )
}
