import { Modal } from 'react-bootstrap';
import React, { useState, useEffect } from 'react'
import Loading from '../base/Loading'
import { Form } from 'react-bootstrap';
import { TextInput } from 'react-bootstrap4-form-validation';
import { Col } from 'react-bootstrap';
import { Table } from 'react-bootstrap';
import moment from 'moment';
import callApi from '../../../helpers/conectorApi';
import PdfContainer from '../../../helpers/PdfContainer';
import Doc from '../../../helpers/DocService';
export const VistaPreviaPartida = ({ abrirModal, setAbrirModal, partida }) => {
    const [loading, setLoading] = useState(false);
    const [detallePartida, setDetallePartida] = useState([]);
    const [totalDebe, setTotalDebe] = useState(0);
    const [totalHaber, setTotalHaber] = useState(0);

    const GetInfoPartida = async () => {
        setLoading(true);
        let response = await callApi(`partidacontable/${partida.partida_contableId}`);
        if (response) {
            setDetallePartida(response);
        }
        setLoading(false);
    }

    useEffect(() => {
        let sumDebe = 0;
        let sumHaber = 0;
        detallePartida.map(({ monto }) => {
            if (Number(monto) > 0) {
                sumDebe += Number.parseFloat(monto);
            }
            if (Number(monto) < 0) {
                sumHaber += Math.abs(Number.parseFloat(monto));
            }
        });
        setTotalDebe(sumDebe);
        setTotalHaber(sumHaber);

    }, [detallePartida])

    useEffect(() => {
        GetInfoPartida();
    }, []);

    const createPdf = (html) => Doc.createPdf(html);

    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)} size="lg">
            {
                loading === true ?
                    <Loading />
                    : <>
                        <Modal.Header closeButton>
                            <Modal.Title as="h5">Información</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <PdfContainer createPdf={createPdf}>
                                <>
                                    <h5>Comprobante contable</h5>
                                    <hr />
                                    <Form.Row>
                                        <Form.Group as={Col} md="6">
                                            <Form.Label htmlFor="fecha"><strong>Fecha:</strong> {moment(partida.fecha).format('DD/MM/YYYY') || ""}</Form.Label>
                                        </Form.Group>
                                        <Form.Group as={Col} md="6">
                                            <Form.Label htmlFor="numeroPartida"><strong>No. Partida:</strong> {partida.numero_partida}</Form.Label>
                                        </Form.Group>
                                    </Form.Row>

                                    <Form.Row>
                                        <Form.Group as={Col} md="12">
                                            <Form.Label htmlFor="observaciones"><strong>Observaciones:</strong> <br></br> {partida.observaciones || ""}</Form.Label>
                                        </Form.Group>
                                    </Form.Row>


                                    <h5>Detalle</h5>
                                    <hr />
                                    <Table striped hover responsive bordered id="tableSucursal">
                                        <thead>
                                            <tr>
                                                <th>Linea</th>
                                                <th>Codigo</th>
                                                <th>Cuenta</th>
                                                <th>Debe</th>
                                                <th>Haber</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                detallePartida.map(({ partida_contable_detalleId, monto, Cuenta }, index) => {
                                                    let { codigo, nombre } = Cuenta;
                                                    let montoDebe = 0;
                                                    let montoHaber = 0;
                                                    if (Number(monto) > 0) {
                                                        montoDebe = monto;
                                                    } else if (Number(monto) < 0) {
                                                        montoHaber = Math.abs(Number(monto));
                                                    }
                                                    return (
                                                        <tr key={partida_contable_detalleId}>
                                                            <td>{index + 1}</td>
                                                            <td>{codigo}</td>
                                                            <td>{nombre}</td>
                                                            <td>Q {Number(montoDebe).toFixed(2)}</td>
                                                            <td>Q {Number(montoHaber).toFixed(2)}</td>
                                                        </tr>
                                                    )
                                                })
                                            }
                                        </tbody>
                                        <tfoot>
                                            <tr>
                                                <td colSpan="3">Totales</td>
                                                <td>Q <strong>{Number(totalDebe).toFixed(2)}</strong></td>
                                                <td>Q <strong>{Number(totalHaber).toFixed(2)}</strong></td>
                                            </tr>
                                        </tfoot>
                                    </Table>
                                </>
                            </PdfContainer>
                        </Modal.Body>
                    </>
            }
        </Modal>
    )
}
